import Vue from 'vue'
import Vuex from 'vuex'
import axios from '../plugins/axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    online: window.navigator.onLine,
    user: null,
    sessions: [],
    current: {
      sessionIdx: null,
      exoIdx: null,
    },
    voices: [],
  },
  mutations: {
    online (state, value) {
      state.online = value
    },
    setUser (state, user) {
      state.user = user
      if (user) {
        localStorage.setItem('user', JSON.stringify(user));
      } else {
        localStorage.removeItem('user');
      }
    },

    setVoices (state) {
      new Promise(
        function (resolve, reject) {
          let synth = window.speechSynthesis;
          let id;

          id = setInterval(() => {
            if (synth.getVoices().length !== 0) {
              resolve(synth.getVoices());
              clearInterval(id);
            }
          }, 10);
        }
      ).then((voices) => state.voices = voices)
    },

    setSessions (state, sessions) {
      state.sessions = sessions
    },
    setCurrentSession (state, idx) {
      state.current.sessionIdx = idx
    },
    setCurrentExo (state, idx) {
      state.current.exoIdx = idx
    },
    incCurrentExo (state) {
      state.current.exoIdx = state.current.exoIdx + 1
    },
    resetCurrent (state) {
      state.current = {
        sessionIdx: null,
        exoIdx: null,
      }
    }
  },
  getters: {
    isOnline: (state) => {
      return state.online
    },

    getVoices: (state) => {
      return state.voices
    },

    getUser: (state) => {
      let user
      if (!state.user && (user = localStorage.getItem('user'))) {
        state.user = JSON.parse(user)
      }
      return state.user
    },

    getSession: (state) => (idx) => {
      return state.sessions.find(session => session.id == idx)
    },
    getCurrentExoIdx: (state) => {
      return state.current.exoIdx
    }
  },
  actions: {
    login() {
      return new Promise(( res, rej ) => {
        axios
          .get('/login.json')
          .then(r => r.data)
          .then(user => {
            this.commit('setUser', user)
            res(user)
          })
          .catch(() => rej());
        })
    },
    logout() {
      this.commit('setUser', null)
    },

    loadSessions() {
      axios
        .get('/sessions.json')
        .then(r => r.data)
        .then(sessions => {
          this.commit('setSessions', sessions)
        })
    }
  },
  modules: {
  }
})



